import React from "react";
import {Button, Col, Form, Row, Container} from "react-bootstrap";
import Descendencia from "./Descendencia";
import DescendenciaApp from "./DescendenciaApp";
import "./DescendenciasApp.css"
import Sistema from "../sistema/Sistema";

interface Props {
    descendencias: Descendencia[]
    updateDescendencias(descendencias: Descendencia[]):boolean
}

interface State {
    descendencias:Descendencia[]
    descendenciaSelecionada?: Descendencia
    show:boolean[]
    showPlus:boolean
}

export default class DescendenciasApp extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            descendencias:this.props.descendencias,
            show:[],
            showPlus:false
        };
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>, snapshot?: any): void {
        if (JSON.stringify(prevProps.descendencias) !== JSON.stringify(this.props.descendencias))
            this.setState({descendencias: this.props.descendencias})
    }

    updateDescendencia(novaDescendencia: Descendencia, prevDescendencia: Descendencia) {
        let index = this.state.descendencias.findIndex(descendencia => {
            return (
                prevDescendencia.idDescendencia === descendencia.idDescendencia
            )
        });
        if (!this.state.descendencias.find((descendencia) => {
            return descendencia.idDescendencia === novaDescendencia.idDescendencia
        })) {
            this.setState(state => {
                state.descendencias[index] = novaDescendencia;
                return {
                    descendencias: state.descendencias
                }
            });
            return true
        } else {
            return false
        }
    }

    addDescendencia(novaDescendencia: Descendencia) {
        if (!this.state.descendencias.find((descendencia) => {
            return descendencia.idDescendencia === novaDescendencia.idDescendencia
        })) {
            this.setState(state => {
                state.descendencias.push(novaDescendencia);
                return {
                    descendencias: state.descendencias
                }
            });
            return true
        } else {
            return false
        }
    }
    deleteDescendencia(descendencias:Descendencia[], index:number){
        descendencias.splice(index,1);
        this.setState({
            descendencias:descendencias
        })
    }

    /*handleHide(){
        this.setState({show:false})
    }

    /*handleShow(){
        this.setState({show:true})
    }*/

    render() {
        const descendencias = this.state.descendencias;
        return (
            <Container>
                <Row>
                    <Col>
                        <Form.Label column={false}>
                            Descendencias
                        </Form.Label>
                    </Col>
                    <Col>
                        <Button
                            onClick={()=>this.setState({showPlus:true})}
                        >+</Button>
                        <DescendenciaApp
                            descendencia={Sistema.descendencias[0]}
                            updateFunc={this.addDescendencia.bind(this)}
                            show={this.state.showPlus}
                            handleHide={()=>this.setState({showPlus:false})}
                        />
                    </Col>
                </Row>
                {descendencias.map((descendencia, index) => {
                        return (
                            <Row
                                key={index}
                            >
                                <Button
                                    onClick={()=>this.setState(state=>{
                                        state.show[index] = true;
                                        return{
                                        show:state.show
                                        }
                                    })}
                                >
                                    {descendencia.nomeDescendencia}
                                </Button>
                                <Col md={"auto"}>
                                    <DescendenciaApp
                                        updateFunc={this.updateDescendencia.bind(this)}
                                        retorna={(descendenciaR:Descendencia)=>{
                                            console.log(descendenciaR);
                                            console.log(descendencia)
                                        }}
                                        descendencia={descendencia}
                                        show={this.state.show[index]}
                                        handleHide={()=>this.setState(state=>{
                                            state.show[index] = false;
                                            return{show:state.show}
                                        })}
                                    />
                                </Col>
                                <Col md style={{ paddingLeft: 0, paddingRight: 0 }}>
                                    <Button
                                        size={"sm"} variant="outline-danger"
                                        onClick={()=>this.deleteDescendencia(descendencias,index)}
                                    >&#10005;</Button>
                                </Col>
                            </Row>
                        )
                    }
                )}
            </Container>
        );
    }
}
